import React, { Component } from 'react'

export default class Courses extends Component {
    render() {
      return (
        <div>
          <section className="colorlib-experience" data-section="courses">
            <div className="colorlib-narrow-content">
              <div className="row">
                <div className="col-md-6 col-md-offset-3 col-md-pull-3 fadeInLeft animated" data-animate-effect="fadeInLeft">
                  <span className="heading-meta">Orientation</span>
                  <h2 className="colorlib-heading ">Courses</h2>
                </div>
              </div>
              <div className="row">
                <div className="col-md-12">
                  <div className="timeline-centered">
                    <article className="timeline-entry fadeInRight animated" data-animate-effect="fadeInRight">
                      <div className="timeline-entry-inner">
                        <div className="timeline-icon color-13">
                          <i className="icon-pen2" />
                        </div>
                        <div className="timeline-label">
                          <h2><a href="https://www.udemy.com/course/the-web-developer-bootcamp" target="_blank" rel="noopener noreferrer">The Web Developer Bootcamp, Udemy</a></h2> 
                          <h4>July 2019 — September 2019</h4>
                        </div>
                      </div>
                    </article>
                    <article className="timeline-entry fadeInLeft animated" data-animate-effect="fadeInLeft">
                      <div className="timeline-entry-inner">
                        <div className="timeline-icon color-4">
                          <i className="icon-pen2" />
                        </div>
                        <div className="timeline-label">
                        <h2><a href="https://www.udemy.com/course/the-complete-web-developer-zero-to-mastery/" target="_blank" rel="noopener noreferrer">The Complete Web Developer in 2020: Zero to Mastery, Udemy</a></h2> 
                          <h4>November 2019 — January 2020</h4>
                          <img src="https://udemy-certificate.s3.amazonaws.com/image/UC-6AZ4884A.jpg" alt="Nature" className="responsive" />
                          </div>
                      </div>
                    </article>  
                    <article className="timeline-entry begin fadeInRight animated" data-animate-effect="fadeInRight">
                      <div className="timeline-entry-inner">
                        <div className="timeline-icon color-none">
                        </div>
                      </div>
                    </article>
                  </div>
                </div>
              </div>
            </div>
          </section>
        </div>
      )
    }
  }
  