import React, { Component } from 'react'
import { NavLink } from 'react-router-dom';

export default class Footer extends Component {
  render() {
    return (
      <div>
        <footer className="footer" id="colorlib-main">
        <div className="footer-left col-md-6 col-sm-6">
          <p className="about">
            <span> About me</span> Passionate for travelling, I love helping people discover efficient solutions to their problems. I strongly believe that listening to our customers is the best way to improve our products.
          </p>
          <div className="icons">
            <a href="https://www.facebook.com/catalinlungan" target="_blank" rel="noopener noreferrer"><i className="icon-facebook2" /></a>
            <a href="https://www.linkedin.com/in/catalin-lungan-07332058/" target="_blank" rel="noopener noreferrer"><i className="icon-linkedin2" /></a>
            <a href="https://www.instagram.com/lungancatalin/" target="_blank" rel="noopener noreferrer"><i className="icon-instagram" /></a>
            <a href="https://github.com/lungancatalin" target="_blank" rel="noopener noreferrer"><i className="icon-github" /></a>
          </div>
        </div>
        <div className="footer-center col-md-6 col-sm-6">
          <div>
            <i className="icon-location" />
            <p><span> N.Balcescu no.10</span> Alba-Iulia/Cluj, Romania</p>
          </div>
          <div>
            <i className="icon-phone" />
            <p> (+40) 7457 923 97</p>
          </div>
          <div>
            <i className="icon-mail" />
            <p><a href="#"> lungancatalin@yahoo.com</a></p>
          </div>
        </div>
        <div className="col-md-12 col-sm-12">
          <h2> Lungan<span> Catalin-Nicolae</span></h2>
          <p className="menu">
            <NavLink to="/introduction" activeStyle={{fontWeight: "bold", color: "#2c98f0"}}>Introduction</NavLink> |
            <NavLink to="/about" activeStyle={{fontWeight: "bold", color: "#2c98f0"}}>About</NavLink> |
            <NavLink to="/education" activeStyle={{fontWeight: "bold", color: "#2c98f0"}}>Education</NavLink> |
            <NavLink to="/timeline" activeStyle={{fontWeight: "bold", color: "#2c98f0"}}>Timeline</NavLink> |
            <NavLink to="/skills" activeStyle={{fontWeight: "bold", color: "#2c98f0"}}>Skills</NavLink> |
            <NavLink to="/courses" activeStyle={{fontWeight: "bold", color: "#2c98f0"}}>Courses</NavLink>
          </p>
          <div className="colorlib-footer">
              <p><small>
              © Copyright ©2020 All rights reserved
              </small></p>
            </div>
        </div>
      </footer>
      </div>
    )
  }
}
