import React, {Component} from 'react';


export default class About extends Component {
    render() {
      return (
        <div>
        <section className="colorlib-skills" data-section="skills">
            <div className="colorlib-narrow-content">
            <div className="row">
                <div className="col-md-6 col-md-offset-3 col-md-pull-3 fadeInLeft animated" data-animate-effect="fadeInLeft">
                <span className="heading-meta">My Specialty</span>
                <h2 className="colorlib-heading  fadeInUp animated">My Skills</h2>
                </div>
            </div>
            <div className="row">
                <div className="col-md-12  fadeInLeft animated" data-animate-effect="fadeInLeft">
                <p>To obtain a challenging and growth oriented position with my knowledge, experience, and rise to eminence by utilizing my talents with dedication, determination, and sincerity.</p>
                </div>
                <div className="col-md-4 fadeInLeft animated" data-animate-effect="fadeInLeft">
                <div className="progress-wrap">
                    <h3>HTML/CSS</h3>
                    <div className="progress">
                    <div className="progress-bar color-1" role="progressbar" aria-valuenow={70} aria-valuemin={0} aria-valuemax={100} style={{width: '70%'}}>
                        {/* <span>70%</span> */}
                    </div>
                    </div>
                </div>
                </div>
                <div className="col-md-4 fadeInRight animated" data-animate-effect="fadeInRight">
                <div className="progress-wrap">
                    <h3>jQuery</h3>
                    <div className="progress">
                    <div className="progress-bar color-2" role="progressbar" aria-valuenow={60} aria-valuemin={0} aria-valuemax={100} style={{width: '60%'}}>
                    </div>
                    </div>
                </div>
                </div>
                <div className="col-md-4 fadeInLeft animated" data-animate-effect="fadeInLeft">
                <div className="progress-wrap">
                    <h3>WordPress</h3>
                    <div className="progress">
                    <div className="progress-bar color-3" role="progressbar" aria-valuenow={80} aria-valuemin={0} aria-valuemax={100} style={{width: '80%'}}>
                    </div>
                    </div>
                </div>
                </div>
                <div className="col-md-4 fadeInRight animated" data-animate-effect="fadeInRight">
                <div className="progress-wrap">
                    <h3>JavaScript</h3>
                    <div className="progress">
                    <div className="progress-bar color-4" role="progressbar" aria-valuenow={50} aria-valuemin={0} aria-valuemax={100} style={{width: '50%'}}>
                    </div>
                    </div>
                </div>
                </div>
                <div className="col-md-4  fadeInLeft animated" data-animate-effect="fadeInLeft">
                <div className="progress-wrap">
                    <h3>Bootstrap 4</h3>
                    <div className="progress">
                    <div className="progress-bar color-5" role="progressbar" aria-valuenow={70} aria-valuemin={0} aria-valuemax={100} style={{width: '70%'}}>
                    </div>
                    </div>
                </div>
                </div>
                <div className="col-md-4 fadeInRight animated" data-animate-effect="fadeInRight">
                <div className="progress-wrap">
                    <h3>PHP</h3>
                    <div className="progress">
                    <div className="progress-bar color-6" role="progressbar" aria-valuenow={60} aria-valuemin={0} aria-valuemax={100} style={{width: '60%'}}>
                    </div>
                    </div>
                </div>
                </div>
                <div className="col-md-4 fadeInRight animated" data-animate-effect="fadeInRight">
                <div className="progress-wrap">
                    <h3>MySQL</h3>
                    <div className="progress">
                    <div className="progress-bar color-7" role="progressbar" aria-valuenow={55} aria-valuemin={0} aria-valuemax={100} style={{width: '55%'}}>
                    </div>
                    </div>
                </div>
                </div>
                <div className="col-md-4 fadeInRight animated" data-animate-effect="fadeInRight">
                <div className="progress-wrap">
                    <h3>React</h3>
                    <div className="progress">
                    <div className="progress-bar color-8" role="progressbar" aria-valuenow={35} aria-valuemin={0} aria-valuemax={100} style={{width: '35%'}}>
                    </div>
                    </div>
                </div>
                </div>
                <div className="col-md-4 fadeInRight animated" data-animate-effect="fadeInRight">
                <div className="progress-wrap">
                    <h3>Node.js</h3>
                    <div className="progress">
                    <div className="progress-bar color-9" role="progressbar" aria-valuenow={30} aria-valuemin={0} aria-valuemax={100} style={{width: '30%'}}>
                    </div>
                    </div>
                </div>
                </div>
            </div>
            </div>
        </section>
        </div>
      )
    }
  }
  