import React, {Component} from 'react'

export default class Education extends Component {
    render() {
      return (
        <div>
          <section className="colorlib-education" data-section="education">
          <div className="colorlib-narrow-content">
              <div className="row">
              <div className="col-md-12">
                  <div className="row row-bottom-padded-sm fadeInLeft animated" data-animate-effect="fadeInLeft">
                  <div className="col-md-12"> 
                  <div className="about-desc">
                    <span className="heading-meta">Education</span>
                    <h2 className="colorlib-heading">Education</h2>
                  </div>  
                <div className="panel-group margin_0" id="accordion1">
                <div className="panel panel-default">
                    <div className="panel-heading">
                    <h4 className="panel-title">
                        <a className="collapsed" data-toggle="collapse" data-parent="#accordion1" href="#collapse1">
                        <i className="rt-icon2-bubble highlight" />
                            Bachelor's Degree, Computer Science, "1 Decembrie 1918" University / Faculty of Exact and Engineering Sciences / Informatics, Alba-Iulia
                        </a>
                    </h4>
                    </div>
                    <div style={{height: '0px'}} id="collapse1" className="panel-collapse collapse">
                    <div className="panel-body">
                        <h4 style={{paddingBottom: '10px'}}>Period of study: September 2016 — July 2019</h4>
                        <ul>
                                <li>Fundamental algorithms</li>
                                <li>Differential equations and partial derivatives</li>
                                <li>Database</li>
                                <li>Based database management systems</li>
                                <li>Artificial intelligence</li>
                                <li>Techniques of optimization</li>
                                <li>WEB Technologies</li>
                        </ul>
                    </div>
                    </div>
                </div>
                <div className="panel panel-default">
                    <div className="panel-heading">
                    <h4 className="panel-title">
                        <a data-toggle="collapse" data-parent="#accordion1" href="#collapse2" className="collapsed">
                        <i className="rt-icon2-bubble highlight" />
                          Surveyor Engineer, "1 Decembrie 1918" University / Faculty of Exact and Engineering Sciences / Engineering Geodesy / Land and Cadastre Measurements, Alba-Iulia
                        </a>
                    </h4>
                    </div>
                    <div style={{height: '0px'}} id="collapse2" className="panel-collapse collapse">
                    <div className="panel-body">
                    <h4 style={{paddingBottom: '10px'}}>Period of study: September 2008 — July 2012</h4>
                        <ul>
                                <li>Construction surveying</li>
                                <li>Photographic control survey</li>
                                <li>Technical drawing and infographic</li>
                                <li>Applied computing</li>
                                <li>Optimization of geodetic networks</li>
                                <li>Cadastral Information Systems SIC</li>
                                <li>Spatial geodesy and GPS positioning systems</li>
                        </ul>
                    </div>
                    </div>
                </div>
                </div> 
            </div>
                        </div>
                    </div>
                    </div>
                </div>
                </section>
          
        </div>
      )
    }
  }