import React, { Component } from 'react'
import { NavLink } from 'react-router-dom';

export default class Navbar extends Component {
  render() {
    return (
      <div>
        <nav className="navbar navbar-default main-navbar">
          <div className="container-fluid">
            <ul className="nav navbar-nav"> 
              <li><NavLink to="/introduction" activeStyle={{fontWeight: "bold", color: "#2c98f0"}}>Introduction</NavLink></li>
              <li><NavLink to="/about" activeStyle={{fontWeight: "bold", color: "#2c98f0"}}>About</NavLink></li>
              <li><NavLink to="/education" activeStyle={{fontWeight: "bold", color: "#2c98f0"}}>Education</NavLink></li>
              <li><NavLink to="/skills" activeStyle={{fontWeight: "bold", color: "#2c98f0"}}>Skills</NavLink></li>
              <li><NavLink to="/timeline" activeStyle={{fontWeight: "bold", color: "#2c98f0"}}>Timeline</NavLink></li>
              <li><NavLink to="/courses" activeStyle={{fontWeight: "bold", color: "#2c98f0"}}>Courses</NavLink></li> 
            </ul>
          </div>
        </nav>
      </div>
    )
  }
}
